body {
  overflow-x: hidden;
  overflow-y: hidden;
  background-color: #3A6EA5;
}
.App {
  text-align: center;
}
.App-header {
  background-color: #3A6EA5;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-link {
  color: #61dafb;
}

.window {
  background-color: #D6D3CE;
  border-style: ridge;
  border-width: 2px;
  border-color: linear-gradient(white, black);
  border-left-color: white;
  border-top-color: white;
  width: 500px;
  height: 200px;
  padding: 0px;

}

.header {
  background-image: linear-gradient(to right, #0D2669, #3A6EA5);
  color: white;
  margin-top: 0;
  display: flex;
  flex-direction: row;
  font-size: 20px;
  align-self: center;
  padding: 3px;

}

p {
  margin: 0px 0px 0px 0px;
  text-align: left;
}

button {
  background-color: #D6D3CE;
  border-width: 2px;
  border-color: linear-gradient(white, black);
  border-left-color: white;
  border-top-color: white;
  font-size: 10px;
  height: 20px;
  width: 20px;
  align-self: center;
  margin-left: auto;
  padding: 0px;
}

.desktopicon {
  color:white;
  width: 128px;
  height: 72px;
}

.desktopicon>p {
  text-align: center;
}

.desktopicon:hover{
  cursor: pointer;
}

.taskbar {
  width: 100%;
  height: 35px;
  position: absolute;
  z-index: 100;
  bottom: 0;
  background-color: #D6D3CE;
}